    validateFields = (showErrMessage) => {
        let fields = this.state.fields;
        let errors = {};
        let formIsValid = true;
        //companey name
        if (!fields["company_name"]) {
            formIsValid = false;
            errors["company_name"] = "Compony Name cannot be empty";
        }
        //companey Domain
        if (this.state.domainErr) {
            errors["company_domain"] = this.state.domainErr;
            formIsValid = false;
        } else if (!fields["company_domain"]) {
            formIsValid = false;
            errors["company_domain"] = "Compony Domain cannot be empty";
        }
        //fname
        if (!fields["fname"]) {
            formIsValid = false;
            errors["fname"] = "First Name cannot  be empty";
        }
        //lname
        if (!fields["lname"]) {
            formIsValid = false;
            errors["lname"] = "Last Name cannot  be empty";
        }
        //email
        if (!fields["email"]) {
            formIsValid = false;
            errors["email"] = "Email cannot be empty";
        }
        if (typeof fields["email"] !== "undefined") {
            let lastAtPos = fields["email"].lastIndexOf("@");
            let lastDotPos = fields["email"].lastIndexOf(".");
            if (
                !(
                    lastAtPos < lastDotPos &&
                    lastAtPos > 0 &&
                    fields["email"].indexOf("@@") === -1 &&
                    lastDotPos > 2 &&
                    fields["email"].length - lastDotPos > 2
                )
            ) {
                formIsValid = false;
                errors["email"] = "Enter a valid Email";
            }
        }
        //country_code
        if (!fields["country_code"]) {
            formIsValid = false;
            errors["country_code"] = "Country cannot be empty";
        }
        //phone
        if (!fields["phone"]) {
            formIsValid = false;
            errors["phone"] = "Phone Number cannot be empty";
        } else if (isNaN(fields["phone"])) {
            formIsValid = false;
            errors["phone"] = "Enter a valid Phone Number";
        } else if (!fields["phone"].match(/^[0-9]{4,12}$/)) {
            formIsValid = false;
            errors["phone"] = "Enter a valid Phone Number"
        }
        //password
        if (!fields["password"]) {
            formIsValid = false;
            errors["password"] = "Password cannot be empty";
        } else if (!fields['password'].match(/^(?=.*[0-9])(?=.*[!@#$%^&*])[a-zA-Z0-9!@#$%^&*]{6,16}$/)) {
            formIsValid = false;
            errors['password'] = 'Password must contain at least 8 characters with a lowercase, uppercase ,number and a special character'
        }
        //confirm password
        if (!fields["cpassword"]) {
            formIsValid = false;
            errors["cpassword"] = "Confirm Password cannot be empty";
        }
        if (fields['cpassword'] !== fields['password']) {
            formIsValid = false;
            errors["cpassword"] = "Password and Confirm password are not Matching";
        }
        if (!fields['company_logo']) {
            formIsValid = false;
            errors["company_logo"] = "Universal Logo required";
        }
        if (!fields['newsfeed_logo']) {
            formIsValid = false;
            errors["newsfeed_logo"] = "Newsfeed Logo required";
        }


        if (showErrMessage) {
            this.setState({ errors: errors });
        }
        return formIsValid;
    };
