        return new Promise(async (resolve, reject) => {
            let userData = this.encodeBase64();
            const headers = {
                "Content-Type": "application/json",
                "Authorization": appToken(),
            };
            await mykademyApiService
                .authenticateUser(headers, userData, fields['company_domain'])
                .then((response) => {
                    if (response.data.status === 200) {
                        localStorage.setItem("mykademy_subdomain", fields['company_domain']);
                        localStorage.setItem("mykademy_user", JSON.stringify(response.data.data));
                        console.log(response.data.da, 'after auth')
                        this.setState({ mk_member_id: response.data.data.user_data.id }, async () => {
                            // await this.saveMember()
                        })
                        resolve(true)
                    }
                    else {
                        alert('mykademylogin failed!')
                        resolve(false)
                        // microsoftTeams.initialize();
                        // microsoftTeams.authentication.notifySuccess({ success: true });
                    }
                })
                .catch((error) => {
                    console.log(error)
                    // alert('mykademylogin failed!.')
                    resolve(false)

                    // if (myportfolioService.userData()) {
                    //     microsoftTeams.initialize();
                    //     microsoftTeams.authentication.notifySuccess({ success: true });
                    //     resolve(false);
                    // } else {
                    //     return;
                    // }
                });
        });
