    handleChangeSubdomain(field, e) {
        let errors = this.state.errors
        errors[field] = ""
        let fields = this.state.fields;
        fields[field] = e.target.value;
        this.setState({ fields, errors, domainErr: '' }, () => {
            this.subdomainValidation()
        });
        console.log(fields)
    }
    handleChange(field, e) {
        console.log(field, e)
        let errors = this.state.errors
        errors[field] = ""
        let fields = this.state.fields;
        if (field === 'country_code') {
            fields[field] = e.target.value.split('_')[0];
            fields['country'] = e.target.value.split('_')[1];
        } else {
            fields[field] = e.target.value;
        }
        this.setState({ fields, errors });
        console.log(fields)
    }
    UpdateCompanyLogo = (e) => {
        let fields = this.state.fields
        let errors = this.state.errors
        fields['company_logo'] = e.company_logo
        errors['company_logo'] = ""
        this.setState({ fields, errors })
        console.log(e, fields)
    }
    UpdateNewsLogo = (e) => {
        let fields = this.state.fields
        let errors = this.state.errors
        fields['newsfeed_logo'] = e.newsfeed_logo
        errors['newsfeed_logo'] = ""
        this.setState({ fields, errors })
        console.log(e, fields)
    }
    updateState = (e) => {
        console.log(e)
        if (e.company_logo) {
            this.UpdateCompanyLogo(e)
        } else if (e.newsfeed_logo) {
            this.UpdateNewsLogo(e)
        } else {
            this.setState(e)
        }
    }
    subdomainValidation = debounce(async () => {
        this.setState({ apiLoader: true })
        // subdomainValidation = async (e) => {
        console.log(this.state.fields.company_domain, 'this.state.fields.subdomain')
        const yourDate = moment(new Date());
        const newDate = yourDate.format("DD-MM-YYYY");
        const secret = config.mykademy.REACT_APP_MYKADEMY_SUBDOMAIN_VALIDATION_SECRET;
        const data = {
            signature: "mykademy microsoft-teams-app:" + config.mykademy.APP_TOKEN_CREATE,
            app_name: "microsoft teams",
            account_domain: `${this.state.fields.company_domain}.mykademy.com`,
            date: newDate,
        };
        const jwt = sign(data, secret);
        const headers = {
            Authorization: `mykademy microsoft-teams-app:${jwt}`,
        };
        await mykademyApiService
            .domainValidation(headers)
            .then(async (response) => {
                console.log(response)
                if (response !== undefined) {
                    this.setState({ apiLoader: false })
                    let errors = this.state.errors
                    errors['company_domain'] = 'Oops! It looks like this domain is already taken, please try another one.'
                    this.setState({ errors: errors, domainErr: errors['company_domain'] });
                    return
                } else {
                    let formData = new FormData();
                    formData.append('domain', this.state.fields.company_domain)
                    const headers = {}
                    let portfolio_authtoken = myportfolioCommonService.getAuthtoken()
                    if (portfolio_authtoken !== null) {
                        headers['Authorization'] = 'Bearer ' + portfolio_authtoken
                        await myportfolioApiService
                            .checkDomainExists(headers, formData)
                            .then((response) => {
                                this.setState({ apiLoader: false })
                                console.log(response)
                                if (response.data.is_domain_available === 'false') {
                                    let errors = this.state.errors
                                    errors['company_domain'] = 'Oops! It looks like this domain is already taken, please try another one.'
                                    this.setState({ errors: errors, domainErr: errors['company_domain'] });
                                    return
                                }
                            })
                            .catch((e) => {
                                console.log(e)
                            })
                    }
                }
                // if (response.data.status === 200) {
                //     localStorage.setItem(
                //         "mykademy_app_token",
                //         response.data.data.app_signature
                //     );
                // }
            })
            .catch((err) => {
                this.setState({ apiLoader: false })
                console.log(err);
                let errors = this.state.errors
                errors['company_domain'] = 'Oops! Error.'
                this.setState({ errors: errors });

            });
    }, 500);
