import React, { useState } from 'react';
import Cropper from 'react-cropper';
import 'cropperjs/dist/cropper.css';
import { Notification } from '../../../services/common.service';

import img_question from '../../../assets/images/landing/question.svg'
import img_upload_icon from '../../../assets/images/landing/upload_icon.png'
import img_arrow_swipe from '../../../assets/images/landing/arrow_swap.svg'
import img_delete_blue from '../../../assets/images/landing/delete_blue.svg'
import img_preview_img from '../../../assets/images/landing/preview_img.jpg'
const box = {
    display: 'inlineBlock',
    padding: '10px',
    boxSizing: 'border-box'
};
const img_preview = {
    overflow: 'hidden',
    width: '100%',
    float: 'left',
    height: '300px'
};

class ImageCropperNewsFeed extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            image: '',
            cropData: '',
            cropper: '',
            isfileUpload: false
        };
        this.fileUpload = React.createRef();
        this.showFileUpload = this.showFileUpload.bind(this);
    }

    onChange = e => {
        e.preventDefault();
        var extension = e.target.files[0].name.split('.').pop().toLowerCase()
        let allowed_types = ['png', 'jpg', 'jpeg']
        if (!allowed_types.includes(extension)) {
            Notification("Allowed file formates are .png, ,jpg and .jpeg!", "warning", "Warning")
            return
        }
        let files;
        if (e.dataTransfer) {
            files = e.dataTransfer.files;
        } else if (e.target) {
            files = e.target.files;
        }
        const reader = new FileReader();
        reader.onload = () => {
            this.setState({ image: reader.result, isfileUpload: true });
        };
        reader.readAsDataURL(files[0]);
    };

    getCropData = () => {
        console.log(this.state.cropper.getCroppedCanvas().toDataURL());
        if (typeof this.state.cropper !== 'undefined') {
            this.setState({
                cropData: this.state.cropper.getCroppedCanvas().toDataURL()
            }, () => {
                this.props.updateState({ newsfeed_logo: this.state.cropData })
                this.props.updateState({ show_news_cropper: false })

            });
        }
    };
    cancel = () => {
        this.props.updateState({ show_news_cropper: false })

    }
    deleteImage = () => {
        this.setState({ isfileUpload: false, cropData: null, image: '' })

    }
    showFileUpload() {
        this.fileUpload.current.click()
    }

    render() {
        return (
            <div class="modal fade company-logo-modal universal-logo-modal show" id="companylogoModal" tabindex="-1" aria-labelledby="exampleModalLabel" style={{ display: 'block', paddingRight: '15px',overflow:'scroll' }} aria-modal="true" role="dialog">
                <div class="modal-dialog modal-dialog-centered">
                    <div class="modal-content">
                        <div class="modal-body">
                            <div class="company-title-wrap">
                                <h2>Square Newsfeed Logo</h2>
                                <p>Upload and preview your newsfeed logo using the options below</p>
                            </div>

                            <div class="row">
                                <div class="col-md-6">
                                    <div class="logo-content-holder">
                                        <h4>Your Logo</h4>
                                        {
                                            !this.state.isfileUpload ? (

                                                <div class="upload-logo-content">
                                                    <a class="upload-img-wrap" onChange={e => this.onChange(e)}>
                                                        <img class="upload-icon" src={img_upload_icon} alt="upload images" />
                                                            Upload Image
                                                        <input type="file" id="imgInp" accept="image/png, image/jpg, image/jpeg " />
                                                    </a>

                                                    <div class="associated-question-text">
                                                        <img src={img_question} alt="question" />
                                                            Using a square version of your logo will look best.
                                                    </div>
                                                </div>
                                            ) : (
                                                    <React.Fragment>
                                                        <div class="croppie-img-holder" >
                                                            <div id="image-slide-logo" class="croppie-container">
                                                                <div class="cr-boundary" aria-dropeffect="none" style={{ width: '300px', height: '250px' }}>
                                                                    <Cropper
                                                                        style={{ height: 300, width: '100%' }}
                                                                        zoomTo={0}
                                                                        initialAspectRatio={1}
                                                                        preview=".img-preview"
                                                                        src={this.state.image}
                                                                        viewMode={1}
                                                                        guides={true}
                                                                        minCropBoxHeight={10}
                                                                        minCropBoxWidth={10}
                                                                        background={false}
                                                                        responsive={true}
                                                                        autoCropArea={1}
                                                                        checkOrientation={false} // https://github.com/fengyuanchen/cropperjs/issues/671
                                                                        onInitialized={instance => {
                                                                            this.setState({ cropper: instance });
                                                                        }}
                                                                    />
                                                                    <div class="cr-viewport cr-vp-square" tabindex="0" style={{ width: '200px', height: '200px' }}></div>
                                                                    <div class="cr-overlay"></div>
                                                                </div>
                                                            </div>
                                                        </div>


                                                        <div class="remove-slide-wrap">
                                                            <a onClick={this.showFileUpload} class="remove-slide-btn upload-result" style={{ cursor: 'pointer' }} >
                                                                <input onChange={e => this.onChange(e)} type="file" accept="image/png, image/jpg, image/jpeg " id="my_file" style={{ display: "none" }} ref={this.fileUpload} />
                                                                <img for="file" src={img_arrow_swipe} alt="Replace" />Replace
                                                            </a>
                                                            <a class="remove-slide-btn" onClick={() => this.deleteImage()} style={{ cursor: 'pointer' }}>
                                                                <img src={img_delete_blue} alt="Delete" />Delete
                                                            </a>
                                                        </div>
                                                    </React.Fragment>

                                                )
                                        }

                                        {/* <div class="croppie-img-holder" style={{ display: 'none' }}>
                                        <div id="image-slide-logo" class="croppie-container">
                                            <div class="cr-boundary" aria-dropeffect="none" style={{ width: '300px', height: '250px' }}>
                                                <img class="cr-image" alt="preview" aria-grabbed="false" />
                                                <div class="cr-viewport cr-vp-square" tabindex="0" style={{ width: '200px', height: '200px' }}></div>
                                                <div class="cr-overlay"></div>
                                            </div>
                                            <div class="cr-slider-wrap">
                                                <input class="cr-slider" type="range" step="0.0001" aria-label="zoom" />
                                            </div>
                                        </div>

                                        <div class="remove-slide-wrap">
                                            <a class="remove-slide-btn upload-result" href="javascript:void(0);">
                                                <img src={img_arrow_swipe} alt="Replace" />Replace
                                            </a>
                                            <a class="remove-slide-btn" href="javascript:void(0);">
                                                <img src={img_delete_blue} alt="Delete" />Delete
                                            </a>
                                        </div>
                                    </div> */}
                                    </div>
                                </div>
                                <div class="col-md-6">
                                    <h4>Preview</h4>
                                    <div class="preview-img-wrap"  >{
                                        this.state.isfileUpload ? (

                                            <div className=" preview-img-wrap img-preview" style={img_preview}></div>
                                        ) : (

                                                <img src={img_preview_img} id="preview-img" alt="preview image" />
                                            )
                                    }
                                    </div>
                                </div>
                            </div>

                            <div class="company-btn-wrap">
                                <a onClick={() => this.cancel()} class="btn common-course-btn btn-secondary" data-bs-dismiss="modal">Cancel</a>
                                <button onClick={() => this.getCropData()} disabled={!this.state.isfileUpload} class="btn common-course-btn btn-primary" data-bs-dismiss="modal">Save</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            //   <div>
            //     <div style={{ width: '100%' }}>
            //       <input type="file" onChange={e => this.onChange(e)} />
            //       <br />
            //       <br />
            //       <Cropper
            //         style={{ height: 400, width: '100%' }}
            //         zoomTo={2}
            //         initialAspectRatio={1}
            //         preview=".img-preview"
            //         src={this.state.image}
            //         viewMode={1}
            //         guides={true}
            //         minCropBoxHeight={10}
            //         minCropBoxWidth={10}
            //         background={false}
            //         responsive={true}
            //         autoCropArea={1}
            //         checkOrientation={false} // https://github.com/fengyuanchen/cropperjs/issues/671
            //         onInitialized={instance => {
            //           this.setState({ cropper: instance });
            //         }}
            //       />
            //     </div>
            //     <div>
            //       <div style={box} style={{ width: '50%', float: 'right' }}>
            //         <h1>Preview</h1>
            //         <div className="img-preview" style={img_preview} />
            //       </div>
            //       <div
            //         style={box}
            //         style={{ width: '50%', float: 'right', height: '300px' }}
            //       >
            //         <h1>
            //           <span>Crop</span>
            //           <button
            //             style={{ float: 'right' }}
            //             onClick={() => this.getCropData()}
            //           >
            //             Crop Image
            //           </button>
            //         </h1>
            //         <img
            //           style={{ width: '100%' }}
            //           src={this.state.cropData}
            //           alt="cropped"
            //         />
            //       </div>
            //     </div>
            //     <br style={{ clear: 'both' }} />
            //   </div>
        );
    }
}

export default ImageCropperNewsFeed;